 <template>
    <div class="user-table-before-edit">
        <el-dialog title="新增" 
            :visible.sync="dialogVisible" 
            width="33%" 
            :close-on-click-modal=false
            :show-message=false
            @close="onClose"
        >
            <el-form ref="form" :model="form" label-width="120px" :rules="rules" :validate-on-rule-change=false>
                <el-form-item label="姓名：" prop="name">
                    <el-input class="inputStyle" v-model="form.name" disabled></el-input>
                </el-form-item>
                <el-form-item label="性别：" prop="sex">
                    <el-input class="inputStyle" v-model="form.sex" disabled></el-input>
                </el-form-item>
                <el-form-item label="工号：" prop="num" >
                    <el-input class="inputStyle" v-model="form.num"  disabled></el-input>
                </el-form-item>
                <el-form-item label="过期日期：" prop="date">
                    <el-date-picker class="inputStyle" v-model="form.date" type="date"  placeholder="请选择过期日期" clearable></el-date-picker>
                </el-form-item>
                <el-form-item label="过期原因：" prop="reason">
                    <el-input type="textarea" class="inputStyle" v-model="form.reason" placeholder="请输入过期原因" clearable></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="onClose">取 消</el-button>
                <el-button type="primary" @click="onSubmit" :loading="loading">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<style scoped lang="less">
    @import "../userTableBefore.less";
</style>
<script>
    export default {
        name:'user-table-edit',
        data() {
            return {
                form: {
                    name: '',
                    sex: '',
                    num: 0,
                    date:'',
                    reason: ''
                },
                dialogVisible:false,
                loading:false,
                rules: {
                    date: [
                        { required: true, message: '请选择日期', trigger: 'change' }
                    ],
                    reason: [
                        { required: true, message: '请输入过期原因', trigger: 'change' }
                    ]
                }
            }
        },
        methods: {
            onSubmit() {//提交
                let self = this
                this.$refs.form.validate((valid) => {
                    if (valid) {//验证通过，发起后台请求
                        self.loading = true;
                        console.log('请求数据',this.form)
                        setTimeout(function(){
                            //发起请求，成功后
                            self.dialogVisible = false;
                            self.loading = false;
                            self.$emit('onSubmit','success');
                            self.$message({
                                message: '请求成功',
                                type: 'success'
                            });
                        },1000)
                    } else {
                        return false;
                    }
                });
            },
            onOpen(formData){
                this.form = formData;
                this.loading = false;
                this.dialogVisible = true
            },
            onClose(){
                this.$refs.form.resetFields();
                this.loading = false
                this.dialogVisible = false
            }
        }
    }
</script>



