<template>
    <div class="user-table-before">
        <el-row style="margin-bottom:10px">
            <el-button type="primary" @click="handleRefresh">刷新</el-button>
        </el-row>
        <el-table
            :data="tableData"
            :border=true
            tooltip-effect="dark"
            style="width: 100%"
            type="index"
            v-loading="tableLoading"
        >
            <template v-for="(item) in tableHeader">
                <el-table-column v-if="item.name=='reason'" v-bind:label="item.label" v-bind:width="item.width" :resizable=true>
                    <template slot-scope="scope">
                        <el-popover trigger="hover" placement="top">
                            <p>{{ scope.row.reason }}</p>
                            <div slot="reference" class="name-wrapper">
                                <el-tag size="medium">{{ scope.row.reason | sliceText }}</el-tag>
                            </div>
                        </el-popover>
                    </template>
                </el-table-column>
                <el-table-column v-if="item.name=='date'" v-bind:label="item.label" v-bind:width="item.width" :resizable=true>
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 10px">{{ scope.row.date }}</span>
                    </template>
                </el-table-column>
                <el-table-column v-if="item.name=='operation'" v-bind:prop="item.name" v-bind:label="item.label" v-bind:width="item.width" :resizable=true>
                    <template slot-scope="scope">
                        <a href="javascript:;" @click="handleEdit(scope.$index, scope.row)" style="color:#409EFF;font-size:12px"><i class="el-icon-edit"></i> 修改</a>
                        <el-popover
                            placement="top"
                            width="200"
                            v-model="scope.row.visible"
                        ><!-- 由于每一行都有一个删除按钮，所以要每条数据都有一个单独的visible，否则点击一个，全部显示  -->
                            
                            <p><i class="el-icon-warning" style="color:#E6A23C"></i> 确定要删除这条数据吗？</p>
                            <div style="text-align: right; margin: 0">
                                <el-button size="mini" type="text" @click="scope.row.visible = false">取消</el-button>
                                <el-button type="primary" size="mini" :loading="delLoading" @click="handleDel(scope.$index,scope.row)">确定</el-button>
                            </div>
                            <a href="javascript:;" slot="reference"@click="delLoading=false" style="color:#F56C6C;font-size:12px"><i class="el-icon-delete"></i> 删除</a>
                        </el-popover>
                        <a href="javascript:;" @click="handleDetail(scope.$index, scope.row)" style="color:#409EFF;font-size:12px"><i class="el-icon-document"></i> 详情</a>
                    </template>
                </el-table-column>
                <el-table-column v-if="item.name!='reason'&&item.name!='date'&&item.name!='operation'" v-bind:prop="item.name" v-bind:label="item.label" v-bind:width="item.width" v-bind:type="item.name=='index'?'index':''" :resizable=true>
                </el-table-column>
            </template>
        </el-table>
        <el-pagination
            style="margin-top:10px"
            :total="13"
            :page-sizes="[5,10,15]"
            layout="total, sizes, prev, pager, next, jumper"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="1"
            :page-size="10"
        >
        </el-pagination>
        <user-table-before-edit ref="edit" v-on:onSubmit="handleSubmit"></user-table-before-edit>
    </div>
</template>
<script>
    import userTableBeforeEdit from './userTableBeforeEdit';
    export default {
        components:{
            userTableBeforeEdit
        },
        data(){
            return {
                tableHeader:[
                    {label:'#',name:'index',width:'80'},
                    {label:'姓名',name:'name'},
                    {label:'性别',name:'sex'},
                    {label:'工号',name:'num'},
                    {label:'过期日期',name:'date'},
                    {label:'过期原因',name:'reason'},
                    {label:'操作',name:'operation',width:'200'}
                ],
                tableData:[],
                tableLoading:false,
                delLoading:false,
            }
        },
        filters: {//定义管道
            sliceText: function (value) {
                if (!value) return '';
                if(value.length>19){
                    value = value.slice(0,19)+'...';
                }
                return value
            }
        },
        mounted:function(){
            this.handleRefresh()
        },
        methods:{
            //修改弹框
            handleEdit:function(index,value){
                console.log(value)
                this.$refs.edit.onOpen(value)
            },
            //修改提交
            handleSubmit:function(){
                this.handleRefresh()
            },
            //删除
            handleDel:function(index,row){
                this.delLoading = true;
                let self = this;
                let id = row.id;
                setTimeout(function(){//发起服务器请求成功后
                    row.visible = false;
                    self.delLoading = false;
                    self.handleRefresh()
                    self.$message({type:'success',message:'删除成功'})
                },1000)
            },
            //刷新
            handleRefresh:function(){
                //发起后台请求，将请求的数据加载到表格中
                let self = this;
                this.tableLoading = true;
                setTimeout(function(){
                    self.tableData = [
                       {
                            id:1,
                            date: '2016-05-03',
                            name: '王小虎',
                            // address: '上海市普陀区金沙江路 1518 弄',
                            sex:'男',
                            num:23124,
                            reason:'员工离职，不再任职，故而移除员工信息,不可撤销',
                            visible:false
                        },
                        {
                            id:2,
                            date: '2016-05-03',
                            name: '王红',
                            // address: '上海市普陀区金沙江路 1518 弄',
                            sex:'女',
                            num:43231,
                            reason:'该员工被解雇，离职原因是跟领导发生肢体冲突，不再任职，永不录用',
                            visible:false
                        },
                        {
                            id:3,
                            date: '2016-05-03',
                            name: '李虎',
                            // address: '内蒙古鄂尔多斯',
                            sex:'男',
                            num:54631,
                            reason:'该员工不听从领导安排，擅自离岗，永不录用',
                            visible:false
                        },
                        {
                            id:4,
                            date: '2016-05-03',
                            name: '张三',
                            // address: '上海市昌平区望京路 1518 弄',
                            sex:'女',
                            num:28799,
                            reason:'该员工被解雇，离职原因是跟领导发生肢体冲突，不再任职，永不录用',
                            visible:false
                        },
                        {
                            id:5,
                            date: '2016-05-03',
                            name: '李四',
                            // address: '北京市朝阳区',
                            sex:'男',
                            num:22316,
                            reason:'自愿离职',
                            visible:false
                        },
                        {
                            id:6,
                            date: '2016-05-03',
                            name: '赵六',
                            // address: '哈尔滨市12路3街',
                            sex:'男',
                            num:43557,
                            reason:'员工离职，技术不行，不再任职，故而移除员工信息',
                            visible:false
                        },
                    ]
                    self.tableLoading = false
                },1000) 
            },
            //查看详情
            handleDetail:function(index,value){
                //调用父组件方法，切换tab
                this.$emit('handleChangeTab','tableDetail',value.name);
            },
            //分页
            handleSizeChange:function(size){
                console.log('size',size)
            },
            handleCurrentChange:function(current){
                console.log('current',current)
            }
        },
    }
</script>




